'use strict';

// MODULES //

import isCollection from '@stdlib/assert-is-collection';
import { isPrimitive as isPositiveInteger } from '@stdlib/assert-is-positive-integer';
import format from '@stdlib/error-tools-fmtprodmsg';


// MAIN //

/**
* Tests whether a collection contains at least `n` elements which are truthy.
*
* @param {Collection} collection - input collection
* @param {PositiveInteger} n - number of elements
* @throws {TypeError} first argument must be a collection
* @throws {TypeError} second argument must be a positive integer
* @returns {boolean} boolean indicating whether a collection contains at least `n` elements which are truthy
*
* @example
* var arr = [ 0, 0, 1, 2, 3 ];
*
* var bool = some( arr, 3 );
* // returns true
*/
function some( collection, n ) {
	var count;
	var len;
	var i;
	if ( !isCollection( collection ) ) {
		throw new TypeError( format( '0lgBO', collection ) );
	}
	if ( !isPositiveInteger( n ) ) {
		throw new TypeError( format( '0lg4I', n ) );
	}
	len = collection.length;
	count = 0;
	for ( i = 0; i < len; i++ ) {
		if ( collection[ i ] ) {
			count += 1;
			if ( count === n ) {
				return true;
			}
		}
	}
	return false;
}


// EXPORTS //

export default some;
